<template>
	<el-card class="box-card">
		<div slot="header">
			<span>管理员编辑</span>
		</div>
		<el-form ref="form" :rules="rules" :model="form" label-width="80px">
			<el-form-item label="用户名">
				<!-- readonly 只读属性 -->
				<el-input v-model="form.username" readonly></el-input>
			</el-form-item>
			<el-form-item prop="fullname" label="姓名">
				<el-input v-model="form.fullname"></el-input>
			</el-form-item>
			<el-form-item label="角色">
				<el-select v-model="form.role_id" placeholder="请选择角色" :disabled="$route.params.user_id == 1">
					<el-option v-for="item in role_options" :key="item.role_id" :label="item.role_name"
						:value="item.role_id">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="性别">
				<el-radio-group v-model="form.sex">
					<el-radio label="男"></el-radio>
					<el-radio label="女"></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item prop="tel" label="手机">
				<el-input v-model="form.tel"></el-input>
			</el-form-item>
			<el-form-item prop="email" label="邮箱">
				<el-input v-model="form.email"></el-input>
			</el-form-item>
			<el-form-item label="头像">
				<!-- 这一块无axios的辅助功能 -- 麻烦QAQ ===> 上传图片功能，在项目中经常用到，这种应该封装成一个组件 -->
				<!--  action="/upload/common/" ==> 后台接口地址 -- 上传地址 -->
				<!-- action 地址要完整的后台地址 http://localhost:3001/upload/common/ -->
				<!-- 但此处是 经过elementUI 封装的图片上传组件 -- 未经过axios -- 所以不涉及请求拦截器 -- 无header头 -- 所以要设置header头 -- 看elementUI文档 -->
				<!-- headers设置在data()中，简化一下 -->
				<!-- v-bind指令（即:）对于数据类型的绑定特别好用 -->
				<el-upload :on-success="handleUploadSuccess" :on-error="handleUploadError" :data="{ type: 'avatar' }"
					:headers="headers" :before-upload="beforeAvatarUpload" accept="image/*" :show-file-list="false"
					class="avatar-uploader" :action="`${$server}/upload/common/`">
					<img v-if="form.avatar" :src="form.avatar" class="avatar">
					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
			</el-form-item>
			<el-button @click="handleEdit" type="primary">保存修改</el-button>
		</el-form>
	</el-card>
</template>

<script>
// Role 请求 角色 时用
import { Admin, Role } from '@/api';

export default {
	name: 'Edit',
	data() {
		return {
			form: {
				user_id: '',
				username: '',
				fullname: '',
				sex: '',
				role_id: '',
				tel: '',
				email: '',
				avatar: '',
			},
			// 图片上传的请求头设置
			headers: {
				Authorization: `Bearer ${sessionStorage.access_token}`
			},
			// 角色列表
			role_options: [],
			// 表单验证规则
			rules: {
				fullname: [
					{ required: true, message: '请输入姓名！', trigger: 'blur' },
				],
				tel: [
					{ required: true, message: '请输入手机号！', trigger: 'blur' },
					{ pattern: /^1((3|5|8){1}\d{1}|70)\d{8}$/, message: '请正确输入手机号！', trigger: 'blur' }
				],
				email: [
					{ required: true, message: '请输入邮箱！', trigger: 'blur' },
					{ pattern: /^[a-zA-Z0-9][a-zA-Z0-9_]+\@[a-zA-Z0-9]+\.(com|cn|net|com.cn)$/i, message: '请正确输入邮箱！', trigger: 'blur' }
				],
			}
		}
	},
	mounted() {
		// this.$router ==> 实例对象，跳转路由。
		// this.$route ==> 当前页面的路由对象，包含当前页面的参数信息。
		// 获取地址栏参数 ==> this.$route.params
		let { user_id } = this.$route.params;

		// **********最开始 加载角色列表 和 加载管理员个人资料 是分开请求的，造成了Ajax的并行，但是我们希望先请求角色下拉列表**********

		// 初始化个人资料 -- 需要参数 -- 正好把地址栏获取的参数传入（正是加载时所需要的user_id）
		this.init(user_id);
	},
	beforeDestroy() {
	},
	// 组件 内部的 导航守卫之一
	beforeRouteLeave(to, from, next) {
		next();// 放行
	},
	beforeRouteUpdate(to, from, next) {
		// 守卫监听地址栏参数的变化，去渲染新数据 -- 也可使用watch监听器
		let { user_id } = to.params;
		next()
		this.init(user_id);
	},
	methods: {
		async init(user_id) {
			// 加载角色列表 -- 需要先请求！！！ *****为了下面加载个人资料时，角色也加载出来。*****
			// 别名
			let { status: role_status, data: role_data } = await Role.list();
			if (role_status) {
				this.role_options = { ...role_data };
			}
			// 加载管理员个人资料
			let { status: admin_status, data: admin_data } = await Admin.info({ user_id });
			if (admin_status) {
				// 还原表单
				this.form = { ...admin_data };
			}
		},
		// 文件上传前的钩子
		// element 框架文档 -- 长传头像前的操作 -- 一般就是判断一遍图片是否合规
		beforeAvatarUpload(file) {
			// 打印 file 一下 ==> 显示file是上传的图片对象，包含图片的各种信息。 

			// 此处使用一个正则限制图片格式
			let reg = /^image\/(jpeg||png)$/;
			const isJPG = reg.test(file.type);
			// 限制图片大小 小于两兆
			const isLt2M = file.size / 1024 / 1024 < 2;

			if (!isJPG) {
				this.$message.error('上传头像图片只能是 JPG 格式!');
			}
			if (!isLt2M) {
				this.$message.error('上传头像图片大小不能超过 2MB!');
			}
			// 两个限制条件都合规，才会返回 true
			return isJPG && isLt2M;
		},
		// 文件上传失败的时的钩子
		handleUploadError(error, file, fileList) {
			// console.dir(error)
			// 添加了 状态码 报错信息提示
			// 此处 应用了 解构起别名
			let { status, message } = error;
			switch (status) {
				case 400:
					// 因为 error.message 是混杂了其他字符的字符串，但我们只想要其中的提示信息。
					// 所以 用 JSON.parse 把 error.message 转化为 对象去使用。
					let { status, msg } = JSON.parse(message);
					// 信息提示
					this.$message.error(msg);
					break;
				case 401:
					let { code } = JSON.parse(message);
					this.$message.error(`错误代码401,${code},请重新登录`);
					break;
				default:
					break;
			}
		},
		// 文件上传成功时的钩子
		handleUploadSuccess(response, file, fileList) {
			// this.imageUrl = URL.createObjectURL(file.raw); ==> 创建了一个临时地址 ==>不用，后台返回了一个真实地址。
			let { status, msg, src } = response;
			if (status) {
				// 预览头像
				this.form.avatar = src;
				// 信息提示
				this.$message.success(msg);
			}
		},
		// 编辑
		async handleEdit() {
			// 表单验证的第二种写法 ==> 参考 Element UI ==> validate中不传入参数(即回调函数)时
			// 则会返回一个 promise ==> 可使用 try catch 捕获成功与失败 
			try {
				let valid = await this.$refs.form.validate();
				if (valid) {
					let { status, msg } = await Admin.edit({ ...this.form });
					if (status) {
						// 信息提示
						this.$message.success(msg);
						// 跳转回list页面
						this.$router.go(-1);
					} else {
						this.$message.error(msg);
					}
				}
			} catch (error) {

			}
		},
	},
}
</script>

<style lang="less" scoped>
.avatar-uploader .el-upload {
	border: 2px solid #111010;
	border-radius: 6px;
	cursor: pointer;
	position: relative;
	overflow: hidden;
}

.avatar-uploader .el-upload:hover {
	border-color: #409EFF;
}

.avatar-uploader-icon {
	font-size: 28px;
	color: #8c939d;
	width: 178px;
	height: 178px;
	line-height: 178px;
	text-align: center;
}

.avatar {
	width: 178px;
	height: 178px;
	display: block;
}
</style>
